// src/app/api/mock-oidc/.well-known/openid-configuration/route.js
import { NextResponse } from "next/server"

export function GET(request) {
  const baseUrl = new URL(request.url)
  baseUrl.pathname = baseUrl.pathname.replace(/\.well-known\/openid-configuration$/, "")
  const issuer = baseUrl.origin + baseUrl.pathname.replace(/\/$/, "")

  return NextResponse.json({
    issuer,
    authorization_endpoint: `${issuer}/authorize`,
    token_endpoint: `${issuer}/token`,
    userinfo_endpoint: `${issuer}/userinfo`,
    jwks_uri: `${issuer}/jwks`,
    response_types_supported: ["code"],
    subject_types_supported: ["public"],
    id_token_signing_alg_values_supported: ["RS256"],
    token_endpoint_auth_methods_supported: ["client_secret_basic", "client_secret_post"],
    scopes_supported: ["openid", "profile", "email"],
    claims_supported: ["sub", "name", "preferred_username", "email", "picture"],
    grant_types_supported: ["authorization_code", "refresh_token"],
    code_challenge_methods_supported: ["S256"],
  })
}
